import 'react-native';
import React from 'react';
import {render, screen, fireEvent} from '@testing-library/react-native';
import FloatingButton from '../FloatingButton';
import { shallow } from 'enzyme';

describe('Floating Button', () => {

    it('renders correctly on Detail Screen', () => {
      const {getByText, getByTestId} = render(<FloatingButton  />);
      expect(getByTestId('add')).toBeTruthy();
    });

  
     
        it('calls navigation.navigate when pressed', () => {
          const data = { id: 1, name: 'Test' , description:'here is description' ,date: new Date() };
          const navigation = { navigate: jest.fn() };
          const { getByTestId } = render(
            <FloatingButton
              navigation={navigation}
              onPress={() =>
                props.navigation.navigate('Detail', { item: data, screen: 'Add Item' })
              }
            />
          );
          const button = getByTestId('floating-button');
          button.props.onPress();
          expect(navigation.navigate).toHaveBeenCalledWith('Detail', {
            item: data,
            screen: 'Add Item',
          });
        });

  
});


